<template>
  <div>
    <!-- <headerBar :header-data="headerData" /> -->
    <!-- 分包 begin  2 已完成，1未完成 0 进行中 -->
    <van-tabs v-model="activFb" sticky class="package-tabs" style="z-index: 1">
      <van-tab
        v-for="(fbItem, fbIndex) in FbList"
        ref="tabWrappers"
        :key="fbIndex"
      >
        <template v-if="fbIndex !=0" #title>
          <!-- <div class="tab-item done"> -->
          <div :class="getFbTab(fbItem)" @click="cheackFbTab(fbIndex)">
            <div class="tab-item-tit">
              {{ fbItem.bmc }}
            </div>
          </div>
        </template>
        <van-sticky :offset-top="58">
          <div class="scrolltab-more" @click="showPopupFb()">
            <i class="iconfont iconarrow-down" />
          </div>
        </van-sticky>

        <!-- 报价内容 begin -->
        <div v-if="showHZSL" class="container-mini subitem" z-index="9">
          <div class="content">
            <div class="common-main box">
              <div class="page-list single">
                <div class="pagelist-left">税率(%)</div>
                <div class="pagelist-right" @click="showSLDMXZ()">
                  <div class="right-select">
                    <span class="info">{{ showHZSLLaber }}</span>
                    <i class="iconfont iconarrowright" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div v-for="(dataItem, index1) in FBNRList" :key="dataItem.title">
          <div class="common-info">
            <div class="title title-page">
              <div class="title-left">
                <h2>{{ dataItem.title }}</h2>
              </div>
              <div v-if="index1 == 0" class="title-right">
                <van-button
                  type="info"
                  size="mini"
                  plain
                  @click="goWdxmBjLs()"
                >报价历史</van-button>
              </div>
            </div>

            <div
              v-for="(items, dataindexs) in dataItem.data"
              :key="dataindexs"
              class="common-main box"
            >
              <div
                v-for="(dxItems, dxIndexs) in items"
                :key="dxItems.name"
                :class="dxItems.zdlxxh ? 'page-list ' : 'page-list hide'"
              >
                <!-- sjlx 数据类型 1：数字型 3：字符型 12：图片型 15：附件型 20:pdf-->
                <!-- srfs 输入方式 1-输入 2-代码选择 3-公式计算 -->
                <!-- bz 标志 0-输入/文字 1-输入 -->
                <!-- sfbt 是否必填  1-必填 2-非必填-->
                <!-- sfkyxg 是否可以修改-->
                <div v-if="!sfkyxg" class="pagelist-left">{{ dxItems.name }}</div>
                <div v-if="!sfkyxg">
                  <div
                    v-if="
                      dxItems.sjlx === 12 ||
                        dxItems.sjlx === 15 ||
                        dxItems.sjlx === 20
                    "
                    class="pagelist-right right"
                  >
                    <XyUploadPlus
                      ref="uploadEl"
                      :is-show="false"
                      style="width: 100%"
                      :filed="dxItems.bzdmc"
                      :filegroup="dxItems.showvalue"
                      @getFileChangeInfo="getFileChangeInfo"
                    />
                  </div>
                  <div v-else class="pagelist-right">{{ dxItems.showvalue }}</div>
                </div>

                <div v-else class="page-list-flex">
                  <div class="pagelist-left">
                    <span
                      v-if="dxItems.bz == 1 && dxItems.sfbt === 1"
                      class="text-danger"
                    >*</span>{{ dxItems.name }}
                  </div>
                  <div
                    v-if="dxItems.bz === 0"
                    :class="
                      dxItems.sjlx === 12 ||
                        dxItems.sjlx === 15 ||
                        dxItems.sjlx === 20
                        ? 'pagelist-right blue'
                        : 'pagelist-right'
                    "
                  >
                    {{ dxItems.showvalue }}
                  </div>

                  <div v-if="dxItems.bz === 1" class="pagelist-right right">
                    <!-- 处理附件类型 -->
                    <div
                      v-if="
                        dxItems.sjlx === 12 ||
                          dxItems.sjlx === 15 ||
                          dxItems.sjlx === 20
                      "
                    >
                      <div class="uploadregion">
                        <XyUploadPlus
                          :key="key"
                          ref="uploadEl"
                          style="width: 100%"
                          :filed="dxItems.bzdmc"
                          :filegroup="dxItems.showvalue"
                          @getFileChangeInfo="getFileChangeInfo"
                        />
                      </div>
                    </div>
                    <div v-else>
                      <div v-if="dxItems.srfs === 2">
                        <div
                          class="right-select"
                          @click="showDMXZ(index1, dataindexs, dxIndexs, dxItems.gldm)"
                        >
                          <span class="info">{{ dxItems.showvalue }}</span>
                          <i class="iconfont iconarrowright" />
                        </div>
                      </div>
                      <div v-else>
                        <van-field
                          v-model="dxItems.value"
                          :placeholder="getPlaceholder(dxItems)"
                          input-align="right"
                          rows="1"
                          :disabled="dxItems.srfs === 3 ? true : false"
                          :type="getInputType(dxItems)"
                          autosize
                          @input="getInput(index1, dataindexs, dxIndexs)"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          <!-- 多表结束 -->
          </div>
        </div>

      <!-- 报价内容 end -->
      </van-tab>
    </van-tabs>

    <div v-if="sfkyxg || showEditeButton" class="common-space" />
    <!-- 底部按钮 begin -->
    <div v-if="sfkyxg || showEditeButton" class="foot-btn fixed">
      <ul>
        <li>
          <div v-if="fbzjtext" class="text">
            <span class="text-mini">{{ fbzjtext }}：</span><em>{{ fbzj }}</em>
          </div>
          <div v-if="fbzjbhstext" class="text">
            <span class="text-mini">{{ fbzjbhstext }}：</span><em>{{ fbzjbhs }}</em>
          </div>
        </li>

        <li v-if="!showEditeButton" class="btn-spe1">
          <van-button
            type="info"
            round
            size="large"
            @click="submitGysBj(0)"
          >保存报价</van-button>
        </li>
        <li v-if="!showEditeButton" class="btn-spe2">
          <van-button
            type="info"
            round
            size="large"
            @click="submitGysBj(1)"
          >提交</van-button>
        </li>
        <li v-if="showEditeButton">
          <div class="button-action">
            <van-button
              class="action1"
              type="info"
              round
              size="large"
              style="width: 100px"
              @click="updateBjzt()"
            >撤回报价</van-button>
            <van-button
              class="action2"
              type="info"
              round
              size="large"
              style="width: 80px"
              @click="goWdxmBjLs()"
            >报价历史
            </van-button>
          </div>
        </li>
      </ul>
    </div>
    <!-- 底部按钮 end -->

    <!-- 分包列表弹窗 begin -->
    <van-popup
      v-model="showFb"
      round
      closeable
      position="bottom"
      :style="{ height: '60%' }"
    >
      <div class="common-popup">
        <div class="popup-title">分包列表</div>
        <div class="popup-content">
          <div class="package-list">
            <ul>
              <li
                v-for="(fbItem1, fbIndex1) in FbList"
                :key="fbIndex1"
                :class="getFbTab(fbItem1)"
                @click="cheackFbTab(fbIndex1)"
              >
                <span v-if="fbIndex1 !=0 " class="tit">{{ fbItem1.bmc }}</span>
              </li>
            </ul>
          </div>
        </div>
        <!-- 说明文字 begin -->
        <div class="progressblock-tips">
          <ul>
            <li class="done">已完成</li>
            <li class="active">当前分包</li>
            <li>未进行</li>
          </ul>
        </div>
        <!-- 说明文字 end -->
      </div>
    </van-popup>
    <!-- 分包列表弹窗 end -->
    <van-popup
      v-model="showDMXZPOP"
      position="bottom"
      :style="{ height: '50%' }"
    >
      <van-picker
        show-toolbar
        title="请选择"
        :columns="realArray"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>
  </div>
</template>

<script>
// import headerBar from '@/components/header/headerBar'
import { mapState } from 'vuex'
import XyUploadPlus from '@/components/upload/uploadPlus'
import { NativeJS } from '@/common/js/mixin.js'
// import BScroll from 'better-scroll'

export default {
  name: 'WdxmBj',
  components: {
    XyUploadPlus
    // headerBar
    // [InputItem.name]: InputItem
  },
  mixins: [NativeJS],
  data() {
    return {
      key: 0,
      fileChangeInfo: {},
      filegroup: '',
      fileList: [], // 附件下载
      headerData: {
        title: '报价',
        leftUrl: 'back',
        leftText: ' '
      },
      tblcid: '', // 分包伦次id
      showEditeButton: false, // 是否展示修改按钮
      sfkyxg: false, // 是否可以修改
      showMenu: false, // 更多菜单展示
      showFb: false, // 是否展示分包
      showDMXZPOP: false, // 是否展示代码选择
      dmxzArray: [], // 代码选择
      realArray: [], // 代码选择  keys
      positionArray: [], // 弹出框当前位置
      FbList: [{ 'name': '1' }], // 分包list
      activFb: 1, // 当前选中得分包
      FBNRList: {}, // 分包内容
      inputType: '', // 输入类型
      fbzjbhstext: '', // 总价不含税文字
      fbzjtext: '', // 分包总价文字
      fbzj: 0, // 分包总价
      fbzjbhs: 0, // 分包总价不含税
      sjbs: {},
      mbjg: [112, 102, 106, 108, 121, 120], // 目标计算结果种类
      zjHz: [], // 总价汇总
      zjhsHz: [], // 总价不含税汇总
      showHZSL: false, // 是否展示汇总税率
      showHZSLLaber: '请输入税率', // 税率数据
      showHZSLGLDM: '', // 关联代码
      showHZSLIndex: [] // 当前代码
      // 100 物料编码      101 物料名称   103-品牌   104-规格型号  140-型号（规格和型号分开是使用） 106 采购数量
      // 107 计量单位    123 材质
      // 110 明细税率（%）  112 税率（%）
      // 102-单价   116-交货期  117-付款方式    128-综合单价       108-小计        113-总价
      // 120-单价（含税）  121-小计（含税）  122-总价（含税） 124-供应商物料描述
      // 150-询价单文件    151-报价文件 152-币种 155-价格单位 156-供应商物料编码 157-质保期
      // 询价单综合项      201-百公里以下  201-百公里以上  203-基础铜价  204-二次元件和辅材系数  205-其他费系数  206-补材系数
      // 222-优惠率 130 成交量 131 成交比例
      // 141- 税额
    }
  },
  computed: {
    // 取数据
    ...mapState({
      isLogin: (state) => state.mh.mhisLogin,
      userInfo: (state) => state.mh.mhUserInfo,
      xmid: (state) => state.gyswdxm.xmid
    })
  },
  created() {
    //  this.$nextTick(() => {
    this.getFBList()
    // })
  },

  methods: {
    // 上传附件回调函数 name:附件名称，fileChanInfo:附件信息
    getFileChangeInfo(name, fileChangeInfo) {
      console.log('上传回调信息', name, fileChangeInfo)
      for (let j = 0; j < this.FBNRList.length; j++) {
        const arr1 = this.FBNRList[j].data
        // 获取当天填报报价伦次
        if (this.FBNRList[j].type === 2) {
          this.tblcid = this.FBNRList[j].kzzda
        }
        for (let j1 = 0; j1 < arr1.length; j1++) {
          const arr2 = arr1[j1]
          for (let j2 = 0; j2 < arr2.length; j2++) {
            const arr3 = arr2[j2]
            // 判断处理代码选择
            // 判断汇总项是否含有税率并且需要输入
            if (j === 1) {
              if (arr3.bzdmc === name) {
                // 为附件赋值
                console.info(
                  'fileChangeInfo-----111------',
                  fileChangeInfo.filegroup
                )
                this.fileChangeInfo[name] = fileChangeInfo
                console.info(
                  'fileChangeInfo-----222------',
                  this.fileChangeInfo
                )
                this.FBNRList[j].data[j1][j2].value = this.fileChangeInfo
              }
            }
          }
        }
      }
      // this.fileChangeInfo[name] = fileChangeInfo
      // console.log('123', name, JSON.stringify(this.fileChangeInfo))
    },
    // 控制菜单显示/隐藏
    showMenuBtn() {
      this.showMenu = !this.showMenu
    },
    goWdxm() {
      console.info('Wdxm========')
      this.$router.push({
        name: 'Wdxm'
      })
    },
    // 获取错误信息
    getErrorInput(item) {
      return '请输入' + item.name
    },
    // 获取是否必填
    getIsRequired(item) {
      if (item.bz === 1 && item.sfbt === 1) {
        return true
      } else {
        return false
      }
    },
    // 获取tab分包状态
    getFbTab(item) {
      if (item.activFb === true) {
        if (item.tbzt === 2) {
          return ' tab_item done active'
        } else {
          return 'tab_item active '
        }
      } else {
        if (item.tbzt === 2) {
          return ' tab_item done '
        } else {
          return 'tab_item '
        }
      }
    },

    // 切换分包
    cheackFbTab(index) {
      this.showHZSL = false
      this.showHZSLGLDM = ''
      this.showHZSLIndex = []
      this.fbzjbhstext = ''
      this.fbzjtext = ''
      this.showFb = false
      this.activFb = index
      this.fbzj = 0
      this.fbzjbhs = 0
      this.zjHz = [] // 总价汇总
      this.zjhsHz = [] // 总价不含税汇总
      this.fileChangeInfo = {} // 附件清空
      for (let i = 0; i < this.FbList.length; i++) {
        this.FbList[i].activFb = false
      }
      this.FbList[index].activFb = true
      this.getFBNRList(this.FbList[index].id)
    },
    // 获取输入类型
    getInputType(item1) {
      // 1=数字类型
      // if (item1.sjlx === 1) {
      //   if (item1.xsw === 0) {
      //     console.info('number====', 'number')

      //     return 'number'
      //   } else {
      //     console.info('digit====', 'digit')
      //     return 'digit'
      //   }
      // } else {
      return 'textarea'
      // }
    },
    // 获取Placeholder
    getPlaceholder(item1) {
      if (item1.srfs === 3) {
        return item1.name
      } else {
        return '请输入' + item1.name
      }
    },
    // 获取输入内容
    getInput(index, indexs, dxIndexs) {
      this.getMaths(index, indexs, dxIndexs)
    },
    getMaths(index, indexs, dxIndexs) {
      this.sjbs = {}
      const dxIndexsData = this.FBNRList[index].data[indexs]
      // const sjbs = []// 个数，单价 单价不含税,sl 税率 小计  小计不含税
      for (let i = 0; i < dxIndexsData.length; i++) {
        const element = dxIndexsData[i]
        switch (element.zdlxxh) {
          // 采购数量
          case 106:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 106) {
              this.sjbs = Object.assign({ srx: 'gs' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              { gs: element.value, gsIndex: i, gsxsw: element.xsw },
              this.sjbs
            )
            break
          // 单价
          case 102:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 102) {
              this.sjbs = Object.assign({ srx: 'dj' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              {
                djsrfs: element.srfs,
                dj: element.value,
                djIndex: i,
                djxsw: element.xsw
              },
              this.sjbs
            )
            break
          // 小计
          case 108:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 108) {
              this.sjbs = Object.assign({ srx: 'xj' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              {
                xjsrfs: element.srfs,
                xj: element.value,
                xjIndex: i,
                xjxsw: element.xsw
              },
              this.sjbs
            )
            break
          // 单价（含税）
          case 120:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 120) {
              this.sjbs = Object.assign({ srx: 'djhs' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              {
                djhssrfs: element.srfs,
                djhs: element.value,
                djhsIndex: i,
                djhsxsw: element.xsw
              },
              this.sjbs
            )
            break
          // 小计（含税）
          case 121:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 121) {
              this.sjbs = Object.assign({ srx: 'xjhs' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              {
                xjhssrfs: element.srfs,
                xjhs: element.value,
                xjhsIndex: i,
                xjhsxsw: element.xsw
              },
              this.sjbs
            )
            break
          // 税率（%）
          case 112:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 112) {
              this.sjbs = Object.assign({ srx: 'sl' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              {
                slsrfs: element.srfs,
                sl: element.value,
                slIndex: i,
                slxsw: element.xsw
              },
              this.sjbs
            )
            break
          // 明细税率（%）
          case 110:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 110) {
              this.sjbs = Object.assign({ srx: 'mxsl' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              {
                mxslsrfs: element.srfs,
                mxsl: element.value,
                mxslIndex: i,
                mxslxsw: element.xsw
              },
              this.sjbs
            )
            break
          // 税额
          case 141:
            if (this.FBNRList[index].data[indexs][dxIndexs].zdlxxh === 141) {
              this.sjbs = Object.assign({ srx: 'se' }, this.sjbs)
            }
            this.sjbs = Object.assign(
              {
                sesrfs: element.srfs,
                se: element.value,
                seIndex: i,
                sexsw: element.xsw
              },
              this.sjbs
            )
            break
          default:
            break
        }
      }
      // 获取汇总项税率
      for (let zhIndex = 0; zhIndex < this.FBNRList[1].data.length; zhIndex++) {
        const element = this.FBNRList[1].data[zhIndex]
        for (let j = 0; j < element.length; j++) {
          const ele = element[j]
          if (ele.zdlxxh === 112) {
            this.sjbs = Object.assign(
              { slsrfs: ele.srfs, sl: ele.value, slIndex: j, slxsw: ele.xsw },
              this.sjbs
            )
          }
        }
      }
      // 判断 当前输入项是单价（含税），存在数量，并且拥有小计（含税）项，所以是单价（含税）*数量=小计（含税）
      if (
        this.sjbs.srx === 'djhs' &&
        this.sjbs.xjhsIndex &&
        this.sjbs.xjhssrfs === 3
      ) {
        // 求小计
        const num = this.sjbs.djhs * this.sjbs.gs
        this.FBNRList[index].data[indexs][
          this.sjbs.xjhsIndex
        ].value = parseFloat(num.toFixed(this.sjbs.xjxsw))
        this.zjhsHz[indexs] = parseFloat(num.toFixed(this.sjbs.xjxsw))
      }
      // console.info('sjbs=====', this.sjbs)
      // 判断 当前输入项是单价，存在数量，并且拥有小计项，所以是单价*数量=小计
      if (
        this.sjbs.srx === 'dj' &&
        this.sjbs.xjIndex &&
        this.sjbs.xjsrfs === 3
      ) {
        // 求小计
        const num = this.sjbs.dj * this.sjbs.gs
        this.FBNRList[index].data[indexs][this.sjbs.xjIndex].value = parseFloat(
          num.toFixed(this.sjbs.xjxsw)
        )
        this.zjHz[indexs] = parseFloat(num.toFixed(this.sjbs.xjxsw))
      }
      // 判断 当前输入项是单价，存在数量，没有小计项直接算总价，所以是单价*数量=小计
      if (this.sjbs.srx === 'dj') {
        // 求小计
        const num = this.sjbs.dj * this.sjbs.gs
        this.zjHz[indexs] = parseFloat(num.toFixed(this.sjbs.xjxsw))
        console.info('sjbs=====', this.sjbs)
      }

      // 根据单价和税率计算含税单价和含税小计
      if (
        this.sjbs.srx === 'mxsl' &&
        this.sjbs.dj &&
        this.sjbs.djhssrfs === 3 &&
        this.sjbs.xjhssrfs === 3
      ) {
        // 求小计
        const sl = this.sjbs.mxsl / 100
        const num = this.sjbs.dj * (1 + parseFloat(sl))
        this.FBNRList[index].data[indexs][
          this.sjbs.djhsIndex
        ].value = parseFloat(num.toFixed(this.sjbs.djhsxsw))
        const nums = num * this.sjbs.gs
        this.FBNRList[index].data[indexs][
          this.sjbs.xjhsIndex
        ].value = parseFloat(nums.toFixed(this.sjbs.xjhssxsw))
        this.zjhsHz[indexs] = this.FBNRList[index].data[indexs][
          this.sjbs.xjhsIndex
        ].value
      }

      // 根据单价含税和税率计算出单价和小计
      if (
        this.sjbs.srx === 'djhs' &&
        this.sjbs.djhs &&
        this.sjbs.djsrfs === 3 &&
        this.sjbs.xjsrfs === 3
      ) {
        // 求小计
        const sl = this.sjbs.sl
        const num = this.sjbs.djhs / (1 + parseFloat(sl)) // 单价
        this.FBNRList[index].data[indexs][this.sjbs.djIndex].value = parseFloat(
          num.toFixed(this.sjbs.djxsw)
        )
        const nums = num * this.sjbs.gs
        this.FBNRList[index].data[indexs][this.sjbs.xjIndex].value = parseFloat(
          nums.toFixed(this.sjbs.xjsxsw)
        )

        const se = (this.sjbs.djhs - num) * this.sjbs.gs
        this.FBNRList[index].data[indexs][this.sjbs.seIndex].value = parseFloat(
          se.toFixed(this.sjbs.sexsw)
        )

        this.zjHz[indexs] = this.FBNRList[index].data[indexs][
          this.sjbs.xjIndex
        ].value
      }

      // 计算总价
      this.getZJ()
    },
    getZJ() {
      // 计算汇总报价
      if (this.zjHz.length > 0) {
        let num = 0
        console.info('含有税率并且需要选择====this.zjHz======', this.zjHz)
        for (let index = 0; index < this.zjHz.length; index++) {
        // console.info('index==', this.zjHz[index])
          const element = this.zjHz[index]
          num = num + parseFloat(element)
        }
        console.info('含有税率并且需要选择====66======', num.toFixed(6))
        this.fbzj = parseFloat(num.toFixed(6))
        console.info('含有税率并且需要选择====77======', this.fbzj)
      } else {
        this.fbzj = parseFloat(this.fbzj)
        this.fbzj = parseFloat(this.fbzj.toFixed(6))
      }
      // 计算含税汇总报价
      let numhs = 0
      for (let index = 0; index < this.zjhsHz.length; index++) {
        // console.info('index==', this.zjhsHz[index])
        const element = this.zjhsHz[index]
        numhs = numhs + parseFloat(element)
      }

      this.fbzjbhs = parseFloat(numhs.toFixed(6))

      // 汇总项
      const hzx = this.FBNRList[1].data
      for (let index = 0; index < hzx.length; index++) {
        const element = hzx[index]
        for (let j = 0; j < element.length; j++) {
          const ele = element[j]
          if (ele.zdlxxh === 113) {
            this.FBNRList[1].data[index][j].value = parseFloat(
              this.fbzj.toFixed(6)
            )
          } else if (ele.zdlxxh === 122) {
            this.FBNRList[1].data[index][j].value = parseFloat(
              this.fbzjbhs.toFixed(6)
            )
          }
        }
      }
      // 数据为空
      this.dmxzArray = {}
      this.realArray = []
      this.positionArray = []
    },
    // 下载附件
    // loadFile(index, indexs, dxIndexs) {
    //   this.NativeDownFiles(this.FBNRList[index].data[indexs][dxIndexs].file[0]
    //   )
    // },
    loadfile(url, name) {
      console.info('url------', url)
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.downloadFile(url)
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('downloadFile', String(url), (data) => {})
      } else {
        this.$toast('开发环境不支持')
      }
    },
    // 获取分包列表
    getFBList() {
      this.$store
        .dispatch('gyswdxm/getGysXmfbListAction', {
          xmid: this.xmid,
          zzid: this.userInfo.zzid
        })
        .then((res) => {
          for (let index = 0; index < res.length; index++) {
            this.FbList.push(res[index])
            // if (this.FbList[index].tbzt !== 2 && index === 0) {
            //   this.FbList[index].tbzt = 0;
            // } else if (this.FbList[index].tbzt !== 2 && index !== 0) {
            //   this.FbList[index].tbzt = 1;
            // }
          }
          this.FbList[1].activFb = true
          this.activFb = 1
          this.getFBNRList(this.FbList[this.activFb].id)
        })
        .catch((error) => {
          this.$toast.fail(error)
        })
    },
    // 获取当前分包修改状态
    getFBEideZT(fbid) {
      this.$store
        .dispatch('gyswdxm/getXgbtnZtAction', {
          xmid: this.xmid,
          zzid: this.userInfo.zzid,
          fbid: fbid
        })
        .then((res) => {
          this.FbList[this.activFb].tbzt = 2
          this.showEditeButton = res
        })
        .catch((error) => {
          this.$toast.fail(error)
        })
    },
    // 点击分包修改
    updateBjzt(fbid) {
      this.$store
        .dispatch('gyswdxm/updateBjztAction', {
          tblcid: this.FBNRList[1].data[0][0].value,
          zzid: this.userInfo.zzid,
          fbid: this.FbList[this.activFb].id
        })
        .then((res) => {
          this.sfkyxg = true
          this.fbzj = 0
          this.fbzjbhs = 0
          this.showEditeButton = false
          this.FbList[this.activFb].tbzt = 0
          this.FbList[this.activFb].allowPrice = true
          this.getFBNRList(this.FbList[this.activFb].id)
        })
        .catch((error) => {
          this.$toast.fail(error)
        })
    },
    // 获取分包内容
    getFBNRList(fbid) {
      this.zjhsHz = []
      this.zjHz = []
      this.$store
        .dispatch('gyswdxm/getGysBjListAction', {
          xmid: this.xmid,
          zzid: this.userInfo.zzid,
          fbid: fbid
        })
        .then((res) => {
          this.FBNRList = res
          for (let j = 0; j < this.FBNRList.length; j++) {
            const arr1 = this.FBNRList[j].data
            // 获取当天填报报价伦次
            if (this.FBNRList[j].type === 2) {
              this.tblcid = this.FBNRList[j].kzzda
            }
            for (let j1 = 0; j1 < arr1.length; j1++) {
              const arr2 = arr1[j1]
              for (let j2 = 0; j2 < arr2.length; j2++) {
                const arr3 = arr2[j2]
                // 判断处理代码选择
                if (this.FbList[this.activFb].tbzt !== 2) {
                  if (arr3.srfs === 2) {
                    if (!arr3.showvalue) {
                      this.FBNRList[j].data[j1][j2].showvalue =
                        '请选择' + arr3.name
                    }
                  }
                }
                if (arr3.zdlxxh === 121) {
                  // 小计不含税
                  if (arr3.value !== '') {
                    this.zjhsHz.push(arr3.value)
                  } else if (arr3.zdlxxh === 122) {
                    this.zjhsHz.push(arr3.showvalue)
                  }
                } else if (arr3.zdlxxh === 108) {
                  // 小计
                  if (arr3.value !== '') {
                    this.zjHz.push(arr3.value)
                  }
                } else if (arr3.zdlxxh === 113) { // 总价不含税
                  this.fbzjtext = arr3.name
                  this.fbzj = arr3.showvalue
                } else if (arr3.zdlxxh === 122) { // 总价含税
                  this.fbzjbhstext = arr3.name
                  this.fbzjbhs = arr3.showvalue
                }
                // 判断汇总项是否含有税率并且需要输入
                if (j === 1) {
                  if (arr3.zdlxxh === 112 && arr3.srfs === 2 && arr3.bz === 1) {
                    // 含有税率并且需要选择
                    this.showHZSL = true
                    if (arr3.value !== '' || arr3.value !== null) {
                      this.showHZSLLaber = arr3.value
                      this.showHZSLGLDM = arr3.gldm
                      this.showHZSLIndex.push(j)
                      this.showHZSLIndex.push(j1)
                      this.showHZSLIndex.push(j2)
                    }
                  }
                }
              }
            }
          }
          // 已完成的分包查看是否可以修改
          if (this.FbList[this.activFb].tbzt === 2) {
            this.getFBEideZT(this.FbList[this.activFb].id)
          } else {
            this.showEditeButton = false
          }
          if (this.FbList[this.activFb].allowPrice) {
            this.sfkyxg = true
          } else {
            this.sfkyxg = false
          }
          // 计算总价
          this.getZJ()
          this.key++
        })
        .catch((error) => {
          // this.pageStyles = 3
          this.$toast.fail(error)
          // this.loading = false
        })
    },
    // 提交报价
    submitGysBj(flag) {
      for (let j = 0; j < this.FBNRList.length; j++) {
        const arr1 = this.FBNRList[j].data
        for (let j1 = 0; j1 < arr1.length; j1++) {
          const arr2 = arr1[j1]
          for (let j2 = 0; j2 < arr2.length; j2++) {
            const arr3 = arr2[j2]
            if (arr3.bz === 1 && arr3.sfbt === 1) {
              if (arr3.value === '' || arr3.value === null) {
                return this.$toast.fail('请输入' + arr3.name)
              }
            }
            if (arr3.zdlxxh === 151) {
              let num = 0
              if (arr3.files) {
                num = num + arr3.files.length
              }
              if (this.fileChangeInfo) {
                if (this.fileChangeInfo[arr3.bzdmc].deletedList) {
                  num = num - this.fileChangeInfo[arr3.bzdmc].deletedList.split(',').length
                  num = num + this.fileChangeInfo[arr3.bzdmc].uploadedList.length
                } else {
                  num = num + this.fileChangeInfo[arr3.bzdmc].uploadedList.length
                }
              }
              if (num > 0) {
                this.FBNRList[j].data[j1][j2].value = this.fileChangeInfo
              } else {
                return this.$toast.fail('请上传报价文件')
              }
            }
          }
        }
      }
      // flag === 0 保存 1提交
      this.$store
        .dispatch(
          flag === 0 ? 'gyswdxm/saveGysBjAction' : 'gyswdxm/submitGysBjAction',
          {
            fbid: this.FbList[this.activFb].id,
            zzid: this.userInfo.zzid,
            tblcid: this.FBNRList[1].data[0][0].value,
            data: this.FBNRList
          }
        )
        .then((res) => {
          this.zjhsHz = []
          this.zjHz = []
          if (flag === 0) {
            this.$toast('报价保存成功')
            this.getFBNRList(this.FbList[this.activFb].id)
          } else {
            this.$toast('报价提交成功')
          }
          if (flag === 1) {
            // this.getFBEideZT(this.FbList[this.activFb].id)
            // this.sfkyxg = false
            this.FbList[this.activFb].tbzt = 2
            this.FbList[this.activFb].allowPrice = false
            this.showEditeButton = false
            this.getFBNRList(this.FbList[this.activFb].id)
          }
        })
        .catch((error) => {
          // this.pageStyles = 3

          this.$toast.fail(error)
          // this.loading = false
        })
    },

    // 上传前数据处理
    beforeRead(file, detail) {
      return true
    },
    // 上传后数据
    afterRead(file, detail) {
      var stringResult = detail.name.split(',')
      console.info('stringResult==》', stringResult)
      const list = []
      if (this.fileList.length === 1) {
        list.push(this.fileList[0].content)
      } else {
        list.push(this.fileList[0].content)
        list.push(this.fileList[1].content)
      }
      this.FBNRList[stringResult[0]].data[stringResult[1]][
        stringResult[2]
      ].value = list
      // this.fileList = []
    },
    // 获取文件名称
    getFileName(index, indexs, dxIndexs) {
      console.info('获取文件名称==》', index, indexs, dxIndexs)
      console.info('获取文件名称==》', index, indexs, dxIndexs)
      return index + ',' + indexs + ',' + dxIndexs
    },

    goWdxmBjLs() {
      this.$router.push({
        name: 'WdxmBjLs',
        params: { xmid: this.xmid, fbid: this.FbList[this.activFb].id }
      })
    },
    // 弹出下拉框
    showSLDMXZ() {
      this.positionArray.push(this.showHZSLIndex[0])
      this.positionArray.push(this.showHZSLIndex[1])
      this.positionArray.push(this.showHZSLIndex[2])
      this.getXLLB(this.showHZSLGLDM)
    },
    // 弹出下拉框
    showDMXZ(index, indexs, dxIndexs, gldm) {
      this.positionArray.push(index)
      this.positionArray.push(indexs)
      this.positionArray.push(dxIndexs)
      this.getXLLB(gldm)
    },
    // 获取下拉列表
    getXLLB(gldm) {
      this.realArray = []
      this.$store
        .dispatch('gyswdxm/getGysCodeAction', gldm)
        .then((res) => {
          console.info('res======', res)
          for (const keys in res) {
            this.realArray.push(res[keys])
          }
          this.dmxzArray = res
          this.showDMXZPOP = true
        })
        .catch((error) => {
          // this.pageStyles = 3

          this.$toast.fail(error)
          // this.loading = false
        })
    },
    findKey(obj, value, compare = (a, b) => a === b) {
      return Object.keys(obj).find((k) => compare(obj[k], value))
    },
    // 弹框确认
    onConfirm(pick) {
      this.showDMXZPOP = false
      this.FBNRList[this.positionArray[0]].data[this.positionArray[1]][
        this.positionArray[2]
      ].showvalue = pick
      this.FBNRList[this.positionArray[0]].data[this.positionArray[1]][
        this.positionArray[2]
      ].value = this.findKey(this.dmxzArray, pick)

      const index = this.positionArray[0]
      const indexs = this.positionArray[1]
      const dxIndexs = this.positionArray[2]
      // 判断如果汇总报价关联代码不为空
      if (this.showHZSLGLDM !== '') {
        this.showHZSLLaber = pick
      }

      // 判断当前项是综合项并且输入项是税率得时候
      if (
        index === 1 &&
        this.FBNRList[1].data[indexs][dxIndexs].zdlxxh === 112
      ) {
        for (let i = 0; i < this.FBNRList[0].data.length; i++) {
          const element = this.FBNRList[0].data[i]
          for (let j = 0; j < element.length; j++) {
            const ele = element[j]
            if (
              ele.zdlxxh === 120 &&
              ele.value !== '' &&
              ele.bz === 1 &&
              ele.srfs === 1
            ) {
              this.getMaths(0, i, j)
            }
            // 判断当前单价具有有效值 并且是输入项
            if (
              ele.zdlxxh === 112 &&
              ele.value !== '' &&
              ele.bz === 1 &&
              ele.srfs === 1
            ) {
              this.getMaths(0, i, j)
            }
          }
        }
      } else {
        this.getMaths(
          this.positionArray[0],
          this.positionArray[1],
          this.positionArray[2]
        )
      }
    },
    // 弹框取消
    onCancel() {
      this.showDMXZPOP = false
      // 数据为空
      this.dmxzArray = {}
      this.realArray = []
      this.positionArray = []
    },
    showPopupFb() {
      this.showFb = true
    }
  }
}
</script>

<style lang="css" scoped>
.foot-btn ul li .van-button.van-button--large {
  font-size: 14px;
}
.common-main:last-child {
  margin-bottom: 0;
}
.package-list ul li:first-child{display: none;}
</style>
